.list {
  margin: 20px 0;
  padding: 10px;
  list-style: none; }
  .list li {
    font-size: 16px;
    border-radius: 5px;
    height: 40px;
    padding-left: 8px;
    line-height: 40px;
    margin-bottom: 10px;
    background-color: #fff;
    position: relative; }
    .list li:last-child {
      margin-bottom: 0; }
    .list li .close {
      display: none;
      position: absolute;
      right: 10px;
      top: 8px; }
    .list li:hover .close {
      display: block; }



