@keyframes spin {
  to {
    -webkit-transform: rotate(1turn); } }

.spinner {
  color: #666;
  text-align: center;
  font-size: 30px;
  margin-top: 60px; }

.loading {
  display: inline-block;
  animation: spin 1s linear infinite; }



