.modal {
  text-align: center;
  padding: 0 !important; }
  .modal:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -4px; }

.modal-dialog {
  display: inline-block;
  text-align: left;
  vertical-align: middle; }

.fade-scale {
  transform: scale(0);
  opacity: 0;
  transform: scale(0.7);
  transition: all 0.3s; }
  .fade-scale.in {
    opacity: 1;
    transform: scale(1); }

.modal-backdrop {
  transition: all 0.3s; }



