input[type="file"] {
  position: absolute;
  left: 0;
  top: 0;
  font-size: 200px;
  opacity: 0;
  cursor: pointer; }

.progress {
  margin-left: 15px; }

.upload-pic {
  max-width: 170px;
  display: block !important;
  overflow: hidden;
  margin-bottom: 10px;
  position: relative;
  cursor: pointer; }

.preview {
  margin-top: 0px !important; }
  .preview img {
    max-width: 300px; }

.crop-img-wrap {
  height: 500px;
  overflow: hidden; }

.remodal {
  padding: 15px 15px 65px;
  margin-bottom: 0; }

.crop-option {
  margin-top: 15px; }
  .crop-option .zoom {
    margin-right: 30px; }
    .crop-option .zoom span {
      margin-left: 10px;
      cursor: pointer; }
    .crop-option .zoom .fa {
      font-size: 16px; }



