.upload-btn {
  position: relative;
  margin-bottom: 10px;
  overflow: hidden; }
  .upload-btn .fa {
    margin-right: 5px; }

.upload-progress {
  margin-left: 15px;
  vertical-align: text-bottom; }

.upload-preview input {
  margin-top: 10px; }

.upload-preview img {
  max-width: 500px;
  max-height: 400px;
  border: 1px solid #ccc;
  object-fit: cover; }

.upload-preview .preview-img {
  position: relative;
  display: inline-block; }
  .upload-preview .preview-img .zoom {
    color: #fff;
    position: absolute;
    right: 15px;
    font-size: 20px;
    bottom: 5px; }

.upload-btn input[type="file"] {
  position: absolute;
  left: 0;
  top: 0;
  font-size: 200px;
  opacity: 0; }



