.list-complete-item {
  color: #000;
  transition: all 1s;
  display: inline-block;
  margin-right: 10px; }

.list-complete-enter, .list-complete-leave-active {
  opacity: 0;
  transform: translateY(30px); }

.list-complete-leave-active {
  position: absolute; }



