.sidebar-nav .fa, .fix-operate .fa {
  margin-right: 10px; }

.slide-fade-enter-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1); }

.slide-fade-leave-active {
  transition: all .3s ease-out; }

.slide-fade-enter, .slide-fade-leave-active {
  transform: translateX(10px);
  opacity: 0; }

.page-content {
  padding: 30px 0 100px; }
  .page-content .table td .btn {
    margin-right: 6px; }
  .page-content .title {
    font-weight: 400;
    margin: 0 0 30px; }
  .page-content .nav-tabs {
    margin: 0 -30px 20px;
    padding-left: 30px; }
    .page-content .nav-tabs a.active, .page-content .nav-tabs .router-link-active {
      color: #555;
      cursor: default;
      background-color: #fff;
      border: 1px solid #ddd;
      border-bottom-color: transparent; }
  .page-content .fix-operate {
    position: fixed;
    width: 100%;
    text-align: center;
    border-top: 1px solid #e7e7e7;
    padding: 20px 0;
    left: 0;
    bottom: 0;
    background: #fff; }



